header {
   // position: sticky;
    padding: 10px;
    background-color: $header-color;
    .point{
        display: inline-block;
    }
}

.logo{
    @include marginPadding(10px, 10px);
    a{
        border : $border;
        padding: 10px;
        font-size: 1.9em;
    }
}

nav{
    height: 100%;
    .menu{
        height: 100%;
        @extend %flexClass;
        align-items: center;
        .point{
            padding: 10px;
            font-size: 1.1em;
            &:hover{
                border-bottom: $border;
            }
        }
    }
}
