.centerContent2{
    @include marginPadding(20px, 20px);
    .article{
        margin: 10px;
        display: flex;
        align-items: flex-end;
        p{
            background-color: $base-color;
            opacity: 0.0;
            padding: 15px;
            &:hover{
                opacity: 0.7;
            }
            span{
                font-size: 1.5em;
                line-height: 1.9em;
            }
        }
    }
    #article1{
        @include background("aron-visuals-1186369-unsplash2");
    }
    #article2{
        @include background("mariano-colombotto-1366332-unsplash3");
    }
    #article3{
        @include background("ben-blennerhassett-300784-unsplash2");
    }
    #article4{
        @include background("simon-migaj-1140153-unsplash2");
    }
    #article5{
        @include background("michal-janek-1106585-unsplash3");
    }
    #article6{
        @include background("angel-origgi-1198705-unsplash1");
    }
    #article7{
        @include background("tim-marshall-1254041-unsplash2");
    }

}