.centerContent1{
    @include background("simon-migaj-1140153-unsplash1");
    display: flex;
    justify-content: space-around;
    .form{
        background-color: $base-color;
        opacity: 0.9;
        @include marginPadding(20px, 20px);
        p{
           // color: $border-color;
           border-bottom: $border;
           padding-bottom: 5px;
            font-size: 1.5em;  
            text-align: center;
        }
        .headerOfForm{
            font-style: italic;
        }
        .country, form{
            p{
                color: black;
            }
            margin: 10px;
        }
        .country, .bottom{
            display: flex;
            justify-content: center;
        }
        .bottom{
            p{
                padding: 10px 25px ;
                font-size: 1.2em;
                background-color: lighten($border-color, 5);
                &:hover{
                    background-color: lighten($border-color, 20);
                }
            }
        }
        input, select{
            @include marginPadding(20px, 10px);
        }
    }
}