footer{
    align-items: center;
    background-color: $footer-color;
    padding: 18px 12px 25px 12px;
    .socialMedia{
        display: inline-block;
        .joinUs{
            @include footerFont();
            text-align: center;
            font-size: 1.5em;
        }
        .socialIcons{
            @extend %flexClass; 
            a{
                margin: 2px;
                opacity: 0.3;
                &:hover{
                    opacity: 0.6;
                }
            }
        }
    }
    .contacts{
        .contactUs{
            @include footerFont();
        }
    }
    .information{
        p{
            @include footerFont();
            a{
                @include footerHover;
                color: $border-color;
            }
        }
    }
}