@mixin footerFont(){
    margin: 10px;
    color: $border-color;
    font-size: 1.2em;
}
@mixin footerHover {
    &:hover{
        color: darken($color: $border-color, $amount: 40)
    }
}
@mixin marginPadding($margin, $padding) {
    margin: $margin;
    padding: $padding;
}
@mixin gridDisplay($columns, $percent) {
    display: grid;
    grid-template-columns: repeat($columns, $percent);
    grid-gap: (100%-$columns*$percent)/($columns - 1);
}
@mixin gridChooseTour2 {
    .centerContent1{
        .form{
            width: 90%;
            @include gridDisplay(12, 7%);
            .country, .headerOfForm{
                grid-column: 3/11;
            }
             .topForm{
                grid-column: 1/7;
            }
            .bottomnForm{
                grid-column: 7/13;
            }
        }
    }
}
@mixin gridChooseTour1 {
    .centerContent1{
        @include gridDisplay(12, 7%);
        .form{
            grid-column: 4/10;
        }
    }
}
@mixin gridChooseTour3 {
    .centerContent1{
        @include gridDisplay(12, 7%);
        .form{
            grid-column: 1/5;
        }
    }
}
@mixin gridArticles1{
    .centerContent2{
        @include gridDisplay(12, 7%);
        grid-template-rows: repeat(3, 400px);
        #article1{
            grid-column: 1/4;
        }
        #article2{
            grid-column: 4/9;
        }
        #article3{
            grid-column: 9/13;
        }
        #article4{
            grid-column: 1/6;
        }
        #article5{
            grid-column: 6/10;
        }
        #article6{
            grid-column: 10/13;
        }
        #article7{
            grid-column: 1/13;
        }
    }
}
@mixin gridArticles2{
    .centerContent2{
        @include gridDisplay(12, 7%);
        grid-template-rows: repeat(3, 400px);
        #article1, #article4{
            grid-column: 1/5;
        }
        #article2, #article5{
            grid-column: 5/9;
        }
        #article3, #article6{
            grid-column: 9/13;
        }
        #article7{
            grid-column: 1/13;
        }
    }
}
@mixin gridArticles3{
    .centerContent2{
        @include gridDisplay(12, 7%);
        grid-template-rows: repeat(2, 400px);
        #article1{
            grid-column: 1/4;
        }
        #article2{
            grid-column: 4/7;
        }
        #article3{
            grid-column: 7/10;
        }
        #article4{
            grid-column: 10/13;
        }
        #article5{
            grid-column: 1/5;
        }
        #article6{
            grid-column: 5/8;
        }
        #article7{
            grid-column: 8/13;
        }
    }
}
@mixin background($file) {
    background-image: url("../img/#{$file}.jpg");
    background-position: center;
}